.skillcontainer {
	display: flex;
	justify-content: center;
	align-items: stretch;
	gap: 0.5em;
}

.skillcontainer .icon {
	display: flex;
	align-items: center;
}

.skillcontainer i {
	position: relative;
	top: 0.1em;
	font-size: 2em;
}

.skill {
	flex-grow: 1;
}

.confidence {
	width:100%;
	height:1.5em;
	display:flex;
	align-items: center;
	gap: 0.5em;
	background-color:var(--bs-gray-300);
}

.confidence > div {
	background-color:black;
	height:100%;
	position: relative;
	width: 0%;
	transition: 1s;
	overflow: hidden;
}

.panel:not(.show) .confidence > div {
	width: 0% !important;
}

.confidence span {
	position:absolute;
	width: 0;
	color:white;
	font-size: 1em;
	right:2em;
}
