
.front {
	display: flex;
	justify-content: center;
	align-items: center;
}

.frontflex {
	display: flex;
	width: 100%;
	gap: 2.5em;
	align-items: flex-end;
	justify-content: center;
}

.navscontainer {
	font-size: min(2em, 2vw);
	margin-left: 5em;
	margin-top: 5em;
}

.about {
	margin-top: calc(min(2em, 2vw) * 5);
}

.about h1 {
	background-color: white;
	color: black;
	padding: 0.25em 2em;
	margin-bottom: 0em;
	display: inline-block;
}

.about p {
	font-size: 1.5em;
	margin-bottom: 0.25em;
}

.about .desc {
	background-color: #000000aa;
	padding: 0.5em 1em;
}


.navs {
	display:flex;
	flex-direction: column-reverse;
	align-items: center;
	transform-origin: 100% 0%;
	transform: skewY(-15deg);
}


.navs button {
	width: 7em;
	letter-spacing: 0.15em;
	border: none;
	padding: 0.25em 0em;
	position: relative;
	transition: 0.5s;
	color: black;
	background-color: white;
	text-transform: uppercase;
}

.navs button:hover {
	transform: translateX(-2.5em);
}


.navs button:active {
	transition: 2s;
	transform: skewY(15deg);
}

.navs button:focus {
	transform: scale(50) skewY(15deg);
	opacity: 0;
}

.navs button:before {
	cursor: pointer;
	content: "";
	width: 2.5em;
	height: 100%;
	background-color: inherit;
	filter: brightness(0.25);
	position: absolute;
	left: -2.5em;
	top: 0px;
	transform-origin: right;
	transform: skewY(45deg);
}

.navs button:after {
	cursor: pointer;
	content: "";
	width: 100%;
	height: 2.5em;
	background-color: inherit;
	filter: brightness(0.75);
	position: absolute;
	left: 0px;
	top: -2.5em;
	transform-origin: bottom;
	transform: skewX(45deg);
}

@media only screen and (max-width: 600px) {
	.front {
		display: block;
	}

  .frontflex {
		margin-top: 1em;
		gap: 4em;
    flex-direction: column;
    align-items: center;
  }

  .about {
  	margin: 1em;
  }

  .navscontainer {
  	font-size: 1.25em;
		margin-top: 3em;
		margin-left: 2.5em;
  }

  .navs {
  	font-size: 1em;
  }
}
